import { Component, HostListener, OnChanges, OnInit } from "@angular/core";
import { CinemaService } from "../../../core/services/cinema.service";
@Component({
  selector: "app-he-thong-rap",
  templateUrl: "./he-thong-rap.component.html",
  styleUrls: ["./he-thong-rap.component.scss"],
})
export class HeThongRapComponent implements OnInit, OnChanges {
  dsHeThongRap: [] = [];
  maHeThong: string = "";
  currentWindowWidth: number;
  @HostListener("window:resize")
  onResize() {
    this.currentWindowWidth = window.innerWidth;
  }
  indexActive: number = 0;
  changeIndex(index) {
    this.indexActive = index;
  }
  constructor(private cinemaService: CinemaService) {}

  layMaHeThong(maHeThongChon) {
    // Chn li h thng rp  truyn li sang component lich-chieu-rap
    this.maHeThong = maHeThongChon;
  }

  //ngOnInIt ch chy 1 ln sau khi khi ng component
  ngOnInit(): void {
    this.currentWindowWidth = window.innerWidth;
    if (this.currentWindowWidth <= 420) {
      this.maHeThong = null; // Nu mn hnh nh th ko st h thng rp ban u
    }
    this.cinemaService.layThongTinHeThongRap().subscribe(
      (res) => {
        this.maHeThong = res[0].maHeThongRap; // St h thng rp ban u khi trnh duyt khi ng
        this.dsHeThongRap = res;
        console.log("dsHeThong: ", this.dsHeThongRap);
      },
      (err) => {
        console.log(err);
      }
    );
  }

  ngOnChanges(): void {}
}
