import {
  Component,
  HostListener,
  Input,
  OnChanges,
  OnInit,
} from "@angular/core";
import { Router } from "@angular/router";
import { AuthenticationService } from "src/app/core/services/authentication.service";

@Component({
  selector: "app-chi-tiet-rap",
  templateUrl: "./chi-tiet-rap.component.html",
  styleUrls: ["./chi-tiet-rap.component.scss"],
})
export class ChiTietRapComponent implements OnInit, OnChanges {
  @Input() chiTietRap: any[] = []; //Ly h thng rp chiu theo phim
  @Input() cumRapChieu: any[] = []; //Ly cc rp u tin  active mc nh
  @Input() chiTietPhim; //Chi tit phim
  lichChieu: any[] = []; //gm ngy v gi
  ngayChieu: any[] = [];
  gioChieu: any[] = [];
  maRap: string = ""; // thay i mu, font khi chn rp khc
  indexActiveCumRap = 0; //Active cm rp u tin
  indexActive = 0; //Active rp u tin
  ngayActive = 0;
  currentUser: any = {};
  currentWindowWidth: number;
  @HostListener("window: resize")
  onResize() {
    this.currentWindowWidth = window.innerWidth;
  }

  changeCumRapIndex(index) {
    //active cm rp c chn
    this.indexActiveCumRap = index;
  }

  changeIndex(index) {
    //active rp chn
    this.indexActive = index;
  }

  changeNgayActive(value) {
    this.ngayActive = value;
  }

  changeRapChieu(value) {
    //value: maHeThongRap chn
    //Thay i cc thng s khi thay i h thng
    this.chiTietRap.forEach((rapItem) => {
      if (rapItem.maHeThongRap === value) {
        this.lichChieu = []; //Reset li mng lichChieu khi chn rp khc
        this.maRap = rapItem.maHeThongRap; //Thay i h thng rp
        this.cumRapChieu = rapItem.cumRapChieu; //Thay i mng cm rap chiu theo rp
        this.lichChieu = this.cumRapChieu[0].lichChieuPhim; //Lch chiu th hin phn t u tin ca mng
        this.setNgayChieu(); //set li ngy gi chiu
      }
    });
  }

  // 2 hm set li ngy v gi chiu
  setNgayChieu() {
    this.ngayChieu = []; //Set ngy chiu v rng
    for (let i = 0; i < this.lichChieu.length; i++) {
      this.ngayChieu.push(this.lichChieu[i].ngayChieuGioChieu.split("T")[0]); //push vo mng ngayChieu ngy chiu t lch chiu
    }
    this.ngayChieu = this.ngayChieu.filter(
      (item, index) => this.ngayChieu.indexOf(item) == index // Xa phn t nhng ngy chiu lp nhau
    );
    this.indexActiveCumRap = 0;
    this.ngayActive = 0;
    this.setGioChieu(this.ngayChieu[0]); // Set li gi chiu hin th l ngy chiu u tin trong mng
  }

  setGioChieu(value) {
    this.gioChieu = this.lichChieu.filter(
      (item) => item.ngayChieuGioChieu.split("T")[0] == value
    ); //Lc gi chiu theo ngy
  }

  changeLichChieu(value) {
    //Thay i lch chiu khi thay i cm rp
    this.cumRapChieu.forEach((cumItem) => {
      console.log("cumItem", cumItem);
      if (cumItem.maCumRap === value) {
        this.lichChieu = []; //Xt li lch chiu bng rng  xa nhng lch chiu trc
        this.lichChieu = cumItem.lichChieuPhim;
        this.setNgayChieu();
      }
    });
  }

  changeTime(value) {
    this.setGioChieu(value);
  }

  datVe(maLichChieu) {
    if (this.currentUser.taiKhoan) {
      this.router.navigate([`/datve/${maLichChieu}`]);
    } else {
      alert("Vui lng ng nhp");
      this.router.navigate(["/"]);
    }
  }

  constructor(private auth: AuthenticationService, private router: Router) {}

  ngOnInit(): void {
    this.currentWindowWidth = window.innerWidth;
    this.maRap = this.chiTietRap[0].maheThongRap; //Chn h thng active ln u
    this.lichChieu = this.cumRapChieu[0].lichChieuPhim;
    this.setNgayChieu();
    this.auth.currentUser.subscribe((res) => (this.currentUser = res));
  }

  ngOnChanges(): void {
    // this.maRap = this.chiTietRap[0].maheThongRap; //Chn h thng active ln u
    // this.setNgayChieu();
    // this.auth.currentUser.subscribe((res) => (this.currentUser = res));
  }
}
